n    /*\n  * DESKTOP STYLING\n  */\n    @include media-breakpoint('lg') {\n      display: none;\n      height: 100%;\n      font-weight: bold;\n      text-transform: uppercase;\n\n      ul.main-navigation {\n        height: 100%;\n        display: flex;\n        align-items: center;\n        // gap: 1rem;\n\n        & > .menu-item {\n          height: 100%;\n          display: flex;\n          align-items: center;\n          padding: 0 1rem;\n\n          &:hover,\n          &:focus-within {\n            background-color: $pf-dark-red;\n            color: $pf-white;\n\n            .caret {\n              background-image: url('../../../images/icons/caret_white.svg');\n            }\n          }\n        }\n\n        & > li > a {\n          font-weight: bold;\n        }\n      }\n\n      @include media-breakpoint('lg') {\n        display: block;\n      }\n\n      // Has submenu\n      .menu-item-has-children {\n        position: relative;\n        width: fit-content;\n\n        .sub-menu {\n          display: block;\n          visibility: hidden;\n          background-color: $pf-dark-red;\n          position: absolute;\n          left: 50%;\n          margin-left: -150px; // Has to be half the sub-menu width to centre.\n          top: 100%;\n          max-height: 0;\n          width: 300px;\n\n          li {\n            &:hover,\n            &:focus {\n              background: $pf-red;\n              color: $pf-white;\n            }\n\n            a {\n              display: inline-block;\n              padding: 1em 2em;\n              width: 100%;\n            }\n          }\n        }\n\n        &.open,\n        &:hover {\n          color: $pf-off-white;\n          background: $pf-dark-red;\n\n          .caret {\n            pointer-events: none;\n            transform: rotate(180deg);\n            transition: rotate 300ms ease-out;\n            background-image: url('../../../images/icons/caret_white.svg');\n          }\n\n          .sub-menu {\n            transition: all 400ms ease;\n            visibility: visible;\n            max-height: 2000px;\n          }\n        }\n\n        &:hover {\n          .sub-menu-btn {\n            pointer-events: none;\n          }\n        }\n      }\n    }\n\n  /*\n  * CHEVRON BUTTON\n  */\n    .sub-menu-btn {\n      display: flex;\n      align-items: center;\n      height: 100%;\n\n      @include media-breakpoint('lg') {\n        margin-left: 0.25em;\n      }\n\n      &:disabled {\n        cursor: none;\n        pointer-events: none;\n      }\n\n      &[aria-expanded='true'] {\n        .caret {\n          transform: rotate(180deg);\n          transition: rotate 300ms ease-out;\n        }\n      }\n\n      &:hover,\n      &:focus {\n        cursor: pointer;\n        background-color: $pf-dark-red;\n      }\n\n    }\n\n    .caret {\n      background-image: url('../../../images/icons/caret_white.svg');\n      height: 25px;\n      width: 25px;\n      background-repeat: no-repeat;\n      background-position: center;\n      transform: rotate(0);\n      transition: rotate 300ms ease-out;\n      display: inline-block;\n\n      @include media-breakpoint('lg') {\n        background-image: url('../../../images/icons/caret_charcoal.svg');\n      }\n    }\n  }\n\n  /*\n  * HAMBURGER MENU\n  */\n  .hamb {\n    cursor: pointer;\n    padding: 1rem 0 1rem 1rem;\n\n    @include media-breakpoint('lg') {\n      display: none;\n    }\n\n    &.hamb-open {\n      .hamb-line {\n        background: transparent;\n      }\n\n      .hamb-line::before {\n        transform: rotate(-45deg);\n        top: 0;\n      }\n\n      .hamb-line::after {\n        transform: rotate(45deg);\n        top: 0;\n      }\n    }\n\n    .hamb-line {\n      background: $pf-charcoal;\n      display: block;\n      height: 2px;\n      position: relative;\n      width: 24px;\n\n      &::before,\n      &::after {\n        background: $pf-charcoal;\n        content: '';\n        display: block;\n        height: 100%;\n        position: abso